body,
div,
dl,
dt,
dd,
ul,
li,
h1,
h2,
h3,
h4,
h5,
h6,
pre,
code,
form,
fieldset,
input,
textarea,
p,
blockquote,
th,
td {
  margin: 0;
  padding: 0;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

fieldset,
img,
abbr {
  border: 0;
}

address,
caption,
cite,
code,
dfn,
em,
strong,
th,
var {
  font-style: normal;
  font-weight: normal;
}

ul li {
  list-style: none;
}

caption,
th {
  text-align: left;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-size: 100%;
  font-weight: normal;
}

sup {
  vertical-align: text-top;
}

sub {
  vertical-align: text-bottom;
}

input,
textarea,
select {
  font-family: inherit;
  font-size: inherit;
  font-weight: inherit;
}

legend {
  color: #000;
}

article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section,
main {
  display: block;
}

img {
  max-width: 100%;
  height: auto;
}

.body {
  font-family: Roboto, sans-serif;
  font-family: "Gotham Pro" sans-serif;
}

.wrapper {
  max-width: 1713px;
  padding-left: 30px;
  padding-right: 30px;
  margin: 0 auto;
}

.debug {
  height: 100vh;
  position: fixed;
  width: 100%;
}

.debug > div {
  height: inherit;
  max-width: 1713px;
  padding-left: 30px;
  padding-right: 30px;
  margin: 0 auto;
}

.debug > div > div {
  height: inherit;
  display: flex;
  flex-wrap: wrap;
  margin-left: -10px;
  margin-right: -10px;
}

.debug > div > div > div {
  box-sizing: border-box;
  margin-left: 10px;
  margin-right: 10px;
  word-wrap: break-word;
  width: calc(8.333333333333334% - 20px);
  height: inherit;
  outline: 1px solid #ff0;
  background: rgba(0, 0, 0, 0.5);
}

h1,
.h1 {
  font-size: 36px;
  text-align: center;
  margin: 30px 0;
}

h2,
.h2 {
  font-size: 34px;
  text-align: center;
  margin: 24px 0;
}

h3,
.h3 {
  font-size: 28px;
  text-align: center;
  margin: 18px 0;
}

h4,
.h4 {
  font-size: 20px;
  text-align: center;
  margin: 10px 0;
}

h5,
.h5 {
  font-size: 18px;
  text-align: center;
  margin: 8px 0;
}

h6,
.h6 {
  font-size: 16px;
  text-align: center;
  margin: 6px 0;
}

#page-preloader {
  position: fixed;
  left: 0;
  top: 0;
  right: 0;
  bottom: 0;
  background: #fff;
  z-index: 100500;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

#page-preloader .spinner {
  width: 100%;
  height: 100%;
  position: absolute;
  left: 40%;
  top: 0;
  background: #f1f1f1;
  margin: -16px 0 0 -16px;
  display: flex !important;
  animation: 7s right ease;
}

@keyframes right {
  10% {
    transform: translateX(10%);
  }

  50% {
    transform: translateX(50%);
  }

  100% {
    transform: translateX(100%);
  }
}

.header {
  height: 500px;
}

.header .wrapper {
  height: 100%;
}

.header .wrapper .header_wrapper_items {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.header .wrapper .header_wrapper_items .header_wrapper_item_logo {
  text-transform: uppercase;
}

.header .wrapper .header_wrapper_items .header_wrapper_item_menu {
  text-transform: uppercase;
  cursor: pointer;
}

.header .wrapper .header_wrapper_items_2 {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 80%;
  text-align: center;
}

.header .wrapper .header_wrapper_items_2 .header_wrapper_item_text .main .element {
  font-size: 24px;
  font-weight: bold;
}

.header .wrapper .header_wrapper_items_2 .header_wrapper_item_text .main .element span {
  color: #c70e0a;
}

.header .wrapper .header_wrapper_photo {
  display: flex;
  justify-content: center;
  animation: 0.8s tremor ease-in-out infinite;
}

.header .wrapper .header_wrapper_photo img {
  cursor: pointer;
}

@keyframes tremor {
  0%, 25%, 100% {
    transform: translateY(-4%);
  }

  50% {
    transform: translateY(8%);
  }
}

.portfolio {
  margin-top: 5rem;
}

.portfolio .wrapper .portfolio_wrapper_title {
  margin-bottom: 52px;
}

.portfolio .wrapper .portfolio_wrapper_title h3 {
  text-align: left;
}

.portfolio .wrapper .portfolio_wrapper_button {
  display: flex;
}

.portfolio .wrapper .portfolio_wrapper_button .button {
  width: 170px;
  height: 41px;
  border-radius: 100px;
  border: 1px solid;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
  margin-left: 15px;
}

.portfolio .wrapper .portfolio_wrapper_button .button:first-child {
  margin-left: 0;
}

.portfolio .wrapper .portfolio_wrapper_items {
  display: flex;
  margin-top: 70px;
  margin-bottom: 210px;
}

.portfolio .wrapper .portfolio_wrapper_items .portfolio_wrapper_item {
  width: 478px;
  height: 245px;
  margin-left: 49px;
  box-shadow: 0 0 5px;
}

.portfolio .wrapper .portfolio_wrapper_items .portfolio_wrapper_item:first-child {
  margin-left: 0;
}

.portfolio .wrapper .portfolio_wrapper_items .green {
  background: #29843f;
}

.portfolio .wrapper .portfolio_wrapper_items .red {
  background: #d2064c;
}

.portfolio .wrapper .portfolio_wrapper_items .black {
  background: #000;
}

.portfolio .wrapper .portfolio_wrapper_items .blue {
  background: #00a6e6;
}

.contacts {
  background: #f5f5f5;
}

.contacts .wrapper {
  height: 88%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.contacts .wrapper .contacts_wrapper_block {
  width: 748px;
  height: 300px;
}

.contacts .wrapper .contacts_wrapper_block .contacts_wrapper_title {
  margin-bottom: 85px;
}

.contacts .wrapper .contacts_wrapper_block .contacts_wrapper_items {
  display: flex;
  height: 74%;
}

.contacts .wrapper .contacts_wrapper_block .contacts_wrapper_items .contacts_item {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.contacts .wrapper .contacts_wrapper_block .contacts_wrapper_items .contacts_item input {
  background: none;
  border-bottom: 1px solid #bebebe !important;
  border: none;
  margin-bottom: 30px;
  width: 280px;
  font-size: 18px;
  padding-bottom: 20px;
}

.contacts .wrapper .contacts_wrapper_block .contacts_wrapper_items .contacts_item input::placeholder {
  display: block;
  text-align: center;
}

.contacts .wrapper .contacts_wrapper_block .contacts_wrapper_items .contacts_item input:focus {
  outline: none;
}

.contacts .wrapper .contacts_wrapper_block .contacts_wrapper_items .contacts_item .button {
  width: 170px;
  height: 41px;
  border-radius: 100px;
  border: 1px solid;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
  text-transform: uppercase;
  font-size: 15px;
}

.contacts .wrapper .contacts_wrapper_block .contacts_wrapper_items .contacts_wrapper_item {
  height: 100%;
  width: 100%;
}

.contacts .wrapper .contacts_wrapper_block .contacts_wrapper_items .contacts_wrapper_item:first-child {
  border-right: 1px solid black;
}

.contacts .wrapper .contacts_wrapper_block .contacts_wrapper_items .contacts_wrapper_item .information p:first-child {
  font-size: 18px;
}

.contacts .wrapper .contacts_wrapper_block .contacts_wrapper_items .contacts_wrapper_item .information p:nth-child(2) {
  color: #d5d5d5;
  font-size: 15px;
  font-weight: bold;
}

.contacts .footer {
  background: #000;
  height: 82px;
}

.contacts .footer .wrapper {
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.contacts .footer .wrapper .footer_wrapper_info {
  color: white;
  font-size: 14px;
}

.contacts .footer .wrapper .footer_wrapper_social {
  display: flex;
}

.contacts .footer .wrapper .footer_wrapper_social div {
  width: 30px;
  height: 30px;
  border-radius: 50%;
  background: white;
  margin-left: 15px;
  cursor: pointer;
}

